/*TOSTADAS CARRITO*/

.hideCard {
    visibility: hidden;
    opacity: 0;
    display: none;
}

.showCard {
    display: block;
    opacity: 1;
    visibility: visible;
}

.cartCard {
    border-radius: 10px;
    box-shadow:  0 1px 6px rgb(0 0 0 / 20%);
    width: 25vw;
    height: 25vh;
    position: absolute;
    z-index: 5;
    top: 13vh;
    right: 2.5vw;
    background-color: #f8f6f4;
}


.topToast {
    border-bottom: 1px solid lightgray;
    height: 60%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: .5vw;

}

.infoCart {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.2vh;
    height: 100%;
}

.bold {
    font-weight: 400;
}
.total {
    display: flex;
    justify-content: space-between;
    padding: 0.5vw;
}

.bottomToast {
    height: fit-content;
}

.buttonContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 2vh;
}

.buttonContainer button{
  border: 0;
  background: #d4c5bf;
  color: #7e6661;
  padding: 1vh;
  font-size: 1.5vmin;
  font-weight: 300;
  width: 80%;
  margin: 1vh auto;
  display: block;
  cursor: pointer;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  border-radius: 2px;
  margin-bottom: 1vh;
}

.close {
    width: 2.5vw;
    height: 2.5vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    align-self: flex-start;
    cursor: pointer;
}
.close button {
    background: #d4c5bf;
    border: 0;
    color: #7e6661;
    padding: 1vh;
    font-size: 1.5vmin;
    font-weight: 300;
    width: 80%;
    margin: 1vh auto;
    display: block;
    cursor: pointer;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    border-radius: 2px;
}

.buttonContainer button:hover {
    background: #bf988f;
    color: #fff;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
  }